@font-face {
  font-weight: normal;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed.woff2") format("woff2"), url("../fonts/RobotoCondensed.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2"), url("../fonts/RobotoCondensed-Bold.woff"); }

@font-face {
  font-weight: normal;
  font-family: "PT Mono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTMono.woff2") format("woff2"), url("../fonts/PTMono.woff") format("woff"); }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  text-align: center;
  color: #666666;
  background-color: #ffffff; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

.page-header {
  padding-top: 50px;
  background-color: #1B2931;
  background-image: url("../img/header-back.png"); }

.main-nav {
  width: 1024px;
  margin: 0 auto;
  margin-bottom: 175px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.main-nav__link {
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }
  .main-nav__link:hover, .main-nav__link:focus {
    opacity: 0.8; }
  .main-nav__link:active {
    opacity: 0.7; }
  .main-nav__link--mail {
    margin-left: auto;
    margin-right: 85px; }
  .main-nav__link--phone {
    margin-right: 35px; }

.main-nav__cb {
  width: 180px;
  height: 45px;
  padding: 0;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  background-color: transparent;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  outline: none; }
  .main-nav__cb:hover, .main-nav__cb:focus {
    opacity: 0.8; }
  .main-nav__cb:active {
    opacity: 0.7; }
